import { HeadFC, Link, graphql } from "gatsby"
import * as React from "react"
import AppContent from "./AppContent"
import Head_ from "./Head_"

const TagsContent = ({ data,pageContext }: any) => {
    const post = data.allMarkdownRemark.nodes.map((item: any) =>
        <div className="pt-2 relative outline-1 transition-all hover:outline-2 overflow-hidden dark:hover:outline-blue-400 hover:outline-blue-400 outline outline-gray-300 dark:outline-gray-800 rounded-xl md:pl-2 flex flex-col gap-1 items-center md:items-start pb-2 select-none" key={item.frontmatter.id}>
            <Link to={"/posts/" + item.frontmatter.id} className="text-lg dark:text-gray-50 z-[1] transition-all font-semibold">{item.frontmatter.title}</Link>
            <div className="text-gray-700 flex dark:text-gray-400 flex-col gap-1 z-[1]">
                <div className="text-center md:text-start">{item.frontmatter.date}</div>
            </div>
        </div>)
    return (
        <AppContent setTheme={undefined}>
            <div className="mt-4 mb-4 text-xl font-semibold dark:text-gray-50">标签&nbsp;🏷️:{pageContext.tag}</div>
            <div className="flex flex-col gap-2">{post}</div>
        </AppContent>
    )
}

export const pageQuery = graphql`
query tag($tag:String) {
    allMarkdownRemark(filter:{frontmatter: { tags: { eq: $tag }}}) {
    nodes{
      frontmatter {
        date
        id
        title
      }
    }
    }
  }
  `

  export const Head: HeadFC = ({pageContext}:any) => <><title>标签 | {pageContext.tag}</title><Head_ /><script dangerouslySetInnerHTML={{__html:`if (localStorage.ti == undefined && window.matchMedia('(prefers-color-scheme:dark)').matches || localStorage.ti == 1 && window.matchMedia('(prefers-color-scheme:dark)').matches) {document.documentElement.classList.add('dark')} else if (localStorage.ti==0){document.documentElement.classList.add('dark')}`}}></script></>

export default TagsContent